* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  background-image: linear-gradient(55deg, #87dde8 0%, #a5e2ee 12%, #c0e7f1 27%, #d9ebf2 51%, #eef0f1 100%);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.form-wrap {
  width: 100%;
  z-index: 100;
  background-color: #c7cfd4;
  padding: 8px;
  position: -webkit-sticky;
  position: sticky;
  box-shadow: 0 1px 3px rgba(4, 91, 114, .12), 0 1px 1px rgba(3, 137, 163, .14), 0 2px 1px rgba(241, 241, 241, .2);
}

.search-form {
  width: 100%;
  justify-content: center;
  display: flex;
}

.searchTerm {
  width: 30%;
  height: 36px;
  color: #00b4cc;
  border: 3px solid #00b4cc;
  border-right: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 5px;
  font-size: 20px;
}

.searchButton {
  width: 45px;
  height: 36px;
  cursor: pointer;
  background-color: #00b4cc;
  border: 4px solid #00b4cc;
  border-radius: 0 5px 5px 0;
  justify-content: center;
  align-items: center;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.searchButton:hover, .searchButton:focus {
  background-color: #13c5dc;
  border-color: #13c5dc;
}

.button {
  color: #fafafa;
  background-color: #00b4cc;
  border: 1px solid #00b4cc;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.button:hover, .button:focus {
  background-color: #60dced;
  border-color: #60dced;
}

.button.is-hidden, .button > .spinner.is-hidden {
  display: none;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 30px auto;
  padding: 0;
  display: grid;
}

.photo-card {
  border: 2px solid #c7cfd4;
  border-radius: 10px;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(4, 91, 114, .12), 0 1px 1px rgba(3, 137, 163, .14), 0 2px 1px rgba(241, 241, 241, .2);
}

.photo-card:hover, .photo-card:focus {
  transform: scale(1.05);
}

.info {
  color: #0391a4;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item:not(:last-child) {
  margin-right: 15px;
}

.gallary-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

